.playing-list {
  position: fixed;
  top: 30%;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 200;
  box-shadow: 0 0px 3px rgba(26, 26, 26, 0.1);
  background-color: #f8f8f8;
}
.playing-list .song-num {
  margin-left: 5px;
  font-size: 14px;
  color: #999;
}
.playing-list .am-list {
  height: calc(100% - 105px);
  padding-bottom: 1px;
  box-sizing: border-box;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.playing-list .am-list-item {
  background-color: #f8f8f8;
}
.playing-list .am-list-item .am-list-line {
  padding-right: 0;
}
.playing-list .am-list-item .am-list-line .am-list-content {
  font-size: 16px;
  color: #333;
}
.playing-list .am-list-item .am-list-line .am-list-extra {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-basis: 46px;
          flex-basis: 46px;
  padding: 0;
}
.playing-list .active-song .am-list-line .am-list-content,
.playing-list .active-song .song-artists-alb {
  color: #ffcc00;
}
.playing-list .song-artists-alb {
  font-size: 12px;
  color: #888;
}
.playing-list .btn {
  width: 100%;
  height: 16px;
  background-size: auto 16px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  padding-top: 7px;
  padding-bottom: 7px;
}
.playing-list .close-icon {
  background-image: url(data:image/png;base64,);
}
.playing-list .delete-icon {
  background-image: url(data:image/png;base64,);
}
.playing-list .close-btn {
  padding-left: 0;
}
.playing-list .close-btn .am-list-line .am-list-content {
  text-align: center;
}
.playing-list.slide-up {
  -webkit-transform: translateY(70vh);
          transform: translateY(70vh);
  -webkit-transition: all 250ms cubic-bezier(0, 0.84, 0.36, 1);
  transition: all 250ms cubic-bezier(0, 0.84, 0.36, 1);
}
.playing-list.slide-up-entering {
  -webkit-transform: translateY(70vh);
          transform: translateY(70vh);
}
.playing-list.slide-up-entered {
  -webkit-transform: translateY(0vh);
          transform: translateY(0vh);
}
.playing-list.slide-up-exiting {
  -webkit-transform: translateY(70vh);
          transform: translateY(70vh);
}
.playing-list.slide-up-exited {
  -webkit-transform: translateY(70vh);
          transform: translateY(70vh);
}
