.my-search .am-search {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: #f8f8f8;
}
.my-search .am-search-cancel {
  color: #333;
}
.my-search .search-box {
  position: absolute;
  top: 44px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 85;
  background-color: #fff;
}
.my-search .search-keyword-box,
.my-search .search-result-box,
.my-search .search-loading-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 85;
  background-color: #fff;
}
.my-search .search-keyword-box {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-search .search-keyword-box .hot-keyword,
.my-search .search-keyword-box .history-keyword {
  padding-top: 20px;
  margin: 0 10px;
}
.my-search .search-keyword-box .hot-keyword .title,
.my-search .search-keyword-box .history-keyword .title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px;
}
.my-search .search-keyword-box .hot-keyword .delete-btn,
.my-search .search-keyword-box .history-keyword .delete-btn {
  display: block;
  width: 16px;
  height: 16px;
  padding-left: 16px;
  background-size: auto 16px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}
.my-search .search-keyword-box .hot-keyword .item,
.my-search .search-keyword-box .history-keyword .item {
  display: inline-block;
  padding: 2px 14px;
  margin: 0 10px 10px 0;
  border-radius: 20px;
  background: #eee;
  font-size: 14px;
  color: #333;
  line-height: 2;
}
.my-search .search-result-box {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-search .search-result-box .result-list-item {
  padding-left: 6px;
}
.my-search .search-result-box .result-list-item .am-list-line-multiple {
  padding: 12px 12px 12px 0;
}
.my-search .search-result-box .result-list-item .am-list-line .am-list-content {
  font-size: 14px;
  line-height: 1;
}
.my-search .search-result-box .result-list-item .am-list-line .am-list-brief {
  margin-top: 4px;
  color: #666;
  font-size: 12px;
  line-height: 14px;
}
.my-search .fade {
  -webkit-transition: all 150ms cubic-bezier(0, 0.84, 0.36, 1);
  transition: all 150ms cubic-bezier(0, 0.84, 0.36, 1);
}
.my-search .fade-entering {
  opacity: 0;
}
.my-search .fade-entered {
  opacity: 1;
}
.my-search .fade-exiting {
  opacity: 0;
}
.my-search .fade-exited {
  opacity: 0;
}
