.PlaylistDetail {
  height: 100%;
  background-color: #fff;
}
.PlaylistDetail .my-pld-navbar {
  position: relative;
  z-index: 3;
  width: 100%;
  background-color: transparent;
  background-color: initial;
  color: #ffffff;
}
.PlaylistDetail .scroll-wrapper {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  height: calc(100% - 45px);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.PlaylistDetail .scroll-wrapper .scroll-content {
  min-height: 100%;
  position: relative;
  z-index: 2;
}
.PlaylistDetail .my-plinfo-wrap {
  position: relative;
  z-index: 2;
  display: -webkit-flex;
  display: flex;
  padding: 24px 15px 40px;
}
.PlaylistDetail .my-plinfo-wrap .plinfo-right {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  margin-left: 15px;
}
.PlaylistDetail .my-plinfo-wrap .plinfo-right .title {
  max-height: 0.48rem;
  margin-top: 4px;
  font-size: 0.16rem;
  font-weight: 600;
  color: #fefefe;
  overflow: hidden;
  text-overflow: ellipsis;
}
.PlaylistDetail .my-plinfo-wrap .plinfo-right .author {
  margin-top: 20px;
  font-size: 0.14rem;
  color: hsla(0, 0%, 100%, 0.7);
}
.PlaylistDetail .my-plinfo-wrap .plinfo-right .author .avatar {
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  vertical-align: middle;
  margin-right: 5px;
}
.PlaylistDetail .my-plinfo-wrap .plinfo-right .author .avatar img {
  width: 100%;
  border-radius: 1rem;
}
.PlaylistDetail .playall-btn-wrapper {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 3;
}
.PlaylistDetail .playall-btn-wrapper .playall-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 50px;
}
.PlaylistDetail .playall-btn-wrapper .playall-btn img {
  margin: 0 9px;
}
.PlaylistDetail .playall-btn-wrapper .playall-btn .song-num {
  color: #999;
  font-size: 14px;
  margin-left: 5px;
}
.PlaylistDetail .mty-list-item {
  padding-left: 0;
  background-color: #f8f8f8;
}
.PlaylistDetail .mty-list-item .am-list-line {
  padding: 0;
}
.PlaylistDetail .mty-list-item .am-list-line .am-list-content {
  display: -webkit-flex;
  display: flex;
  padding: 0;
}
.PlaylistDetail .mty-list-item .s-index {
  -webkit-flex: none;
          flex: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 40px;
  font-size: 14px;
  color: #999;
}
.PlaylistDetail .mty-list-item .s-detail-wrap {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  position: relative;
  display: -webkit-flex;
  display: flex;
}
.PlaylistDetail .mty-list-item .s-detail-wrap .s-info {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  padding: 7px 14px 7px 0;
  width: 0;
}
.PlaylistDetail .mty-list-item .s-detail-wrap .s-info .s-name {
  font-size: 16px;
  color: #000;
}
.PlaylistDetail .mty-list-item .s-detail-wrap .s-info .s-artists-alb {
  font-size: 12px;
  color: #888;
}
.PlaylistDetail .bg-wrap {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 40%;
  z-index: 0;
  overflow: hidden;
}
.PlaylistDetail .bg-wrap .fullscreen-blur-bg {
  position: relative;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  -webkit-filter: blur(20px);
          filter: blur(20px);
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.PlaylistDetail .bg-wrap .fullscreen-blur-bg::after {
  content: " ";
  background-color: rgba(0, 0, 0, 0.25);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
}
