.playlists {
  width: 100%;
  height: 100%;
  padding: 0 6px;
  padding-top: 44px;
  box-sizing: border-box;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.playlists .pl-cat {
  margin-top: 22px;
  font-size: 16px;
  font-weight: bold;
}
.playlists .pl-cvrlst {
  width: 100%;
  margin-top: 8px;
  margin-left: -2px;
  font-size: 0;
}
.playlists .pl-cvrlst .pl-li {
  display: inline-block;
  width: 33.333%;
  padding-left: 4px;
  padding-bottom: 18px;
  box-sizing: border-box;
  overflow: hidden;
  line-height: 1.5;
  vertical-align: top;
}
.playlists .pl-cvrlst .pl-li .cover-wrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.playlists .pl-cvrlst .dec {
  margin: 8px 0 3px;
  font-size: 13px;
  color: #000;
}
.playlists .pl-cvrlst .author {
  margin-bottom: 3px;
  font-size: 12px;
  color: #444444;
  white-space: nowrap;
  cursor: default;
}
.playlists .am-list-header {
  padding: 0 2px 8px;
  font-size: 16px;
  color: #000;
  font-weight: bold;
}
.playlists .am-list-item {
  padding-left: 6px;
}
.playlists .am-list-item img {
  width: 60px;
  height: 60px;
  border-radius: 6px;
}
.playlists .am-list-item .am-list-line .am-list-content {
  font-size: 16px;
}
.playlists .am-list-item .am-list-line .am-list-brief {
  font-size: 14px;
}
