.pl-cover {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #e2e2e3;
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
  border-radius: 6px;
}
.pl-cover img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 6px;
}
.pl-cover .play-num {
  position: absolute;
  right: 4px;
  top: 0;
  z-index: 3;
  padding-left: 15px;
  color: #fff;
  font-size: 12px;
  background-position: 0;
  background-repeat: no-repeat;
  background-size: 11px 10px;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
}
.pl-cover .u-earp {
  background-image: url(data:image/svg+xml;base64,);
}
.pl-cover::after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 18px;
  z-index: 2;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background-image: -webkit-linear-gradient(left, transparent, rgba(0, 0, 0, 0.2));
  background-image: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.2));
}
