.fullplayer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 150;
  background: #222;
  overflow: hidden;
}
.fullplayer .my-fullplayer-navbar {
  background-color: transparent;
  background-color: initial;
  max-width: 100%;
}
.fullplayer .my-fullplayer-navbar .am-navbar-title {
  min-width: 75%;
  max-width: 75%;
}
.fullplayer .navbar-title-content {
  min-width: 75%;
  max-width: 75%;
  text-align: center;
}
.fullplayer .navbar-title-content .song-name {
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
}
.fullplayer .navbar-title-content .artists-name {
  font-size: 12px;
  line-height: 1.2;
  color: #f2f2f2;
}
.fullplayer .mid-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  position: fixed;
  top: 45px;
  bottom: 110px;
}
.fullplayer .mid-wrapper .cd-box {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 76%;
}
.fullplayer .mid-wrapper .cd-box .cd-wrapper {
  position: absolute;
  left: 12%;
  top: 0;
  width: 76%;
  height: 100%;
  box-sizing: border-box;
}
.fullplayer .mid-wrapper .cd-box .cd-wrapper .cd {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.fullplayer .mid-wrapper .cd-box .cd-wrapper .cd .image {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 10px solid hsla(0, 0%, 100%, 0.1);
  border-radius: 50%;
}
.fullplayer .mid-wrapper .lyric-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: absolute;
  top: 10px;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -webkit-mask-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.6) 5%, #ffffff 25%, #ffffff 75%, rgba(255, 255, 255, 0.6) 90%, rgba(255, 255, 255, 0) 100%);
}
.fullplayer .mid-wrapper .lyric-box .lyric {
  width: 80%;
  max-width: 80%;
  font-size: 15px;
  text-align: center;
  color: rgba(255, 255, 255, 0.4);
}
.fullplayer .mid-wrapper .lyric-box .lyric .sentence {
  line-height: 1.2;
  padding: 8px 0;
  white-space: normal;
}
.fullplayer .mid-wrapper .lyric-box .lyric .sentence.is-curSentence {
  color: #fff;
}
.fullplayer .mid-wrapper .lyric-box .no-lyric {
  position: absolute;
  top: 10px;
  bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  font-size: 16px;
  color: #fff;
}
.fullplayer .btm-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100%;
}
.fullplayer .btm-wrapper .progress-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 18px;
  margin-bottom: 4px;
  font-size: 10px;
}
.fullplayer .btm-wrapper .progress-wrapper .am-slider-wrapper {
  -webkit-flex: 1 1;
          flex: 1 1;
}
.fullplayer .btm-wrapper .progress-wrapper .time-cur {
  color: #eeeeee;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
}
.fullplayer .btm-wrapper .progress-wrapper .time-dur {
  color: #eeeeee;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
}
.fullplayer .btm-wrapper .operators {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.fullplayer .btm-wrapper .operators .btn {
  width: 48px;
  height: 48px;
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
.fullplayer .btm-wrapper .operators .loop-icon {
  background-image: url(data:image/png;base64,);
  background-size: 48% auto;
}
.fullplayer .btm-wrapper .operators .single-icon {
  background-image: url(data:image/png;base64,);
  background-size: 48% auto;
}
.fullplayer .btm-wrapper .operators .random-icon {
  background-image: url(data:image/png;base64,);
  background-size: 48% auto;
}
.fullplayer .btm-wrapper .operators .prev-icon {
  background-image: url(data:image/png;base64,);
  background-size: 66% auto;
}
.fullplayer .btm-wrapper .operators .play-icon {
  background-image: url(data:image/png;base64,);
}
.fullplayer .btm-wrapper .operators .pause-icon {
  background-image: url(data:image/png;base64,);
}
.fullplayer .btm-wrapper .operators .next-icon {
  background-image: url(data:image/png;base64,);
  background-size: 66% auto;
}
.fullplayer .btm-wrapper .operators .list-icon {
  background-image: url(data:image/png;base64,);
  background-size: 62% auto;
}
.fullplayer .background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.5;
  -webkit-filter: blur(20px);
          filter: blur(20px);
  -webkit-transform: scale(1.5, 1.5);
          transform: scale(1.5, 1.5);
}
.fullplayer .fade {
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.fullplayer .fade-entering {
  opacity: 0;
}
.fullplayer .fade-entered {
  opacity: 1;
}
.fullplayer .fade-exiting {
  opacity: 0;
}
.fullplayer .fade-exited {
  opacity: 0;
}
.fullplayer.slide-up {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
  -webkit-transition: all 250ms cubic-bezier(0, 0.84, 0.36, 1);
  transition: all 250ms cubic-bezier(0, 0.84, 0.36, 1);
}
.fullplayer.slide-up-entering {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
}
.fullplayer.slide-up-entered {
  -webkit-transform: translateY(0vh);
          transform: translateY(0vh);
}
.fullplayer.slide-up-exiting {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
}
.fullplayer.slide-up-exited {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
}
