.miniplayer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.miniplayer::after {
  content: '';
  position: absolute;
  background-color: #ccc;
  display: block;
  z-index: 1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.miniplayer .miniplayer-content {
  padding-left: 6px;
}
.miniplayer .miniplayer-content .am-list-line-multiple {
  padding: 12px 12px 12px 0;
}
.miniplayer .miniplayer-content .am-list-thumb:first-child {
  margin-right: 6px;
}
.miniplayer .miniplayer-content .am-list-line .am-list-content {
  font-size: 14px;
  line-height: 1;
}
.miniplayer .miniplayer-content .am-list-line .am-list-brief {
  margin-top: 4px;
  color: #666;
  font-size: 12px;
  line-height: 14px;
}
.miniplayer .miniplayer-content .am-list-line .am-list-extra {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .btn {
  width: 28px;
  height: 28px;
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .play-icon,
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .pause-icon {
  margin-right: 14px;
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .play-icon {
  background-image: url(data:image/png;base64,);
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .pause-icon {
  background-image: url(data:image/png;base64,);
}
.miniplayer .miniplayer-content .am-list-line .miniplayer-btns-wrapper .playlist-icon {
  background-image: url(data:image/png;base64,);
  background-size: 110% auto;
}
.miniplayer .miniplayer-content img {
  width: 42px;
  height: 42px;
}
.miniplayer.slide-up {
  -webkit-transform: translateY(56px);
          transform: translateY(56px);
  -webkit-transition: all 150ms cubic-bezier(0, 0.84, 0.36, 1);
  transition: all 150ms cubic-bezier(0, 0.84, 0.36, 1);
}
.miniplayer.slide-up-entering {
  -webkit-transform: translateY(56px);
          transform: translateY(56px);
}
.miniplayer.slide-up-entered {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}
.miniplayer.slide-up-exiting {
  -webkit-transform: translateY(56px);
          transform: translateY(56px);
}
.miniplayer.slide-up-exited {
  -webkit-transform: translateY(56px);
          transform: translateY(56px);
}
